export default {"name":"湖南黑砖茶","img":"\\heicha\\hunanheizhuancha\\img_20191122132733b3zlgn.jpg","desc":["黑砖茶，因用黑毛茶作原料，色泽黑润，成品块状如砖，故名。现由湖南白沙溪茶厂独家生产。其原料选自安化、桃江、益阳、汉寿、宁乡等县茶厂生产的优质黑毛茶。白沙茶厂从70年代初对以往费工耗时的繁复工序进行了改革，按原来面茶、里茶的比例一次拼好、一次压制成型，白沙溪于1939年试产黑茶砖成功。制作时先将原料筛分整形，风选拣剔提净，按比例拼配;机压时，先高温汽蒸灭菌，再高压定型，检验修整，缓慢干燥，包装成为砖茶成品。"],"menus":[{"name":"湖南黑砖茶茶叶历史","content":[{"text":"湖南省安化县生产\n        黑茶\n        历史悠久，早在明朝万历年间由户部正式定为运销西北地区以茶易马的\"官茶\"后，陕、甘、宁、晋地区的茶商，到朝廷在各地设置的茶马司以金(货币)易领\"茶引\"(按：明制茶课引规定：上引五千斤、中引四千斤、下引三千斤)，至安化大量采购黑茶砖，运销西北地区以茶易马(按：明洪武二十二年所定茶易马分上、中、下三等：上等马每匹一百二十斤、中等马每匹七十斤、下等马每匹五十斤)。大都运往兰州再转销陕、甘、青、新、宁、藏少数民族地区。明末清初西北地区的\"边茶\"十之八九由安化黑茶供应，多在陕西泾阳压成茶砖。"},{"src":"\\heicha\\hunanheizhuancha\\img_20191122132733y0gl1z.jpg"},{"text":"1939年，湖南省茶叶管理处在安化县设厂大批量生产黑砖茶，产品分\"天、地、人、和\"四级，统称\"黑茶砖\"。1947年，安化茶叶公司设厂于江南镇，在茶砖面上印有\"八\"字，称\"八字茶砖\"，供不应求。新中国成立后，中国茶业公司安化砖茶厂(白沙溪茶厂前身)积极扩大生产，产品改称\"黑砖茶\"，主销西北少数民族地区。"}]},{"name":"湖南黑砖茶茶叶特点","content":[{"text":"黑砖茶的原料，过去分为洒面茶和包心茶两种，洒面茶品质好，包心茶品质差。压制时把差的茶叶压在里面，较好的茶叶压在外面。这样做，内外品质不一，压制也较麻烦。70年代中期，安化白沙溪茶厂进行工艺改革，在提高面茶和包心茶质量的基础上将面茶和包心茶进行混合压制，不分面茶和里茶，做到了品质表里如一，同时又简化了手续，提高了工效。现在压制黑砖茶的原料成份为80%的三级黑毛茶和15%的四级黑毛茶，以及5%的其他茶，总含梗量不超过18%。这些不同级别的毛茶进厂后，要进行筛分、风选、破碎、拼堆等工序，制成合乎规格的半成品，做到形态均匀、质量纯净。半成品再经过蒸压、烘焙、包装等工序，制成黑砖茶。"},{"text":"黑砖茶的外形为长方砖形，规格为35×18×3.5厘米。砖面端正，四角平整，模纹(商标字样)清晰。砖面色泽黑褐，内质香气纯正，滋味浓厚微涩，汤色红黄微暗，叶底老嫩尚匀。每片砖净重2公斤。"}]},{"name":"湖南黑砖茶加工工艺","content":[{"text":"湖南黑砖茶经杀青、初揉、渥堆、复揉、干燥等五道工序而制造成。黑毛茶分为4个级，高档茶较细嫩，低档茶较粗老。湖南黑毛茶内质要求香味醇厚，带松烟香，无粗涩味，汤色橙黄，叶底黄褐。湖南\n        黑茶\n        成品有“三尖”、“四砖”、“花卷“系列。"},{"text":"①三尖："},{"text":"天尖、贡尖、生尖。皆属湖南黑茶之上品,采用 谷雨时节鲜叶加工而成。"},{"text":"天尖：是用一级黑毛茶压制而成， 外形色泽乌润，内质香气清香，滋味浓厚，汤色橙黄，叶底黄褐。"},{"text":"贡尖：是用二级黑毛茶压制而成，外形色泽黑带褐，香气纯正，滋味醇和，汤色稍橙黄，叶底黄褐带暗。"},{"text":"生尖：是用三级黑毛茶压制而成。外形色泽黑褐，香气平淡，稍带焦香，滋味尚浓微涩，汤色暗褐，叶底黑褐粗老。"},{"src":"\\heicha\\hunanheizhuancha\\img_20191122132733nuri05.jpg"},{"text":"②四砖："},{"text":"黑砖茶、花砖茶、茯砖茶、青砖茶，皆属紧压茶类。"},{"text":"黑砖茶：采用安化优质黑毛茶为原料，通过破碎、筛分、发酵、蒸制、机压、烘干等工艺程序精制而成。外形为长方砖形，砖面端正，四角平整，模纹清晰。砖面色泽黑褐油亮，内质香气纯正，滋味浓厚，回味甘甜，汤色红黄透亮，叶底老嫩尚匀。"},{"text":"花砖茶：以安化高山茶为原料，通过破碎、筛分、发酵、蒸制、机压、烘干等工艺程序精制而成。外观上砖面平整、花边及文字图案清晰、棱角分明、厚薄一致、色泽黄褐;内里香气纯正或带松烟香、汤色橙黄或红黄、滋味醇和。"},{"text":"茯砖茶：以安化黑毛茶为原料，通过破碎、筛分、发酵、蒸制、机压、烘干等工艺程序精制而成。松紧均匀，金花蕃芜，香气纯正，滋味醇和微甘，汤色红浓明亮，叶底匀齐尚嫩。随着存放时间的延长，独特的陈香醇味越来越突出，被誉为中国古丝绸之路上的神秘之茶、西北少数民族的生命之茶。"},{"text":"青砖茶：以存放一两年的安化黑毛茶为原料，通过破碎、筛分、发酵、蒸制、机压、烘干等工艺程序精制而成。外观上砖面平整、花纹图案清晰、棱角分明、厚薄一致、色泽为黑褐;内里香气纯正持久、汤色红亮、滋味醇和。"},{"text":"③花卷茶："},{"text":"始名“百两茶”，即一卷(支)茶，净重合老称一百两。后来发展成“千两茶”，即一卷(支)茶，每支净重为老称一千两。好的花卷茶茶身内部生长着一种“菌花”，现已经被证实为“冠突散囊菌”。花卷茶的生产在原料选择上需经筛制，拣剔、整形、拼堆程序，在加工上需经其绞、压、跺、滚、锤工艺，以蓼叶裹胎，外包棕片，再用竹篾捆压箍紧，呈圆柱形状。"}]},{"name":"湖南黑砖茶储藏方法","content":[{"text":"湖南\n        黑茶\n        对存放没有苛刻的要求，平常人家都能存出上好的老茶。如果非要问该怎么储藏安化黑茶，实际上将黑茶放置于阴凉通风防潮防异味处即可。确保茶叶储存“寂静”环境的最好方法是建立标准仓库，给予所收藏的茶叶一个恒温恒湿，有通风换气系统的专业环境。可惜，这对于大多数茶人而言，是可遇不可求的事。好在黑茶属于后发酵茶，茶性相对稳定。只要用心留意下面关于黑茶收藏的基本原则，普通家居环境一样可以保存好黑茶。"},{"text":"【阴凉忌日晒】"},{"text":"阴凉忌日晒，日晒会使茶品急速氧化，产生一些不愉快的化学成分，如日晒味，长时间不得消失。"},{"text":"【通风忌密闭】"},{"text":"通风有助于茶品的自然氧化(发酵)，同时可适当吸收空气的水分(但水分不能过高，否则容易霉变)加速茶体的湿热氧化过程，也为微生物代谢提供水分和氧气。切忌使用塑料袋密封，可用牛皮纸等通透性能较好的包装材料进行包装储存。但也要避免潮湿的环境避免霉变，南方湿热地区尤其需要注意。"},{"text":"【开阔忌异味】"},{"text":"开阔忌异味，茶叶具有极强的吸异性，不能与有异味的物质混放在一起，而宜放置在开阔而通风透气的环境中。除此之外，作为紧压黑茶发源地的湖南安化，为尽量适应湖南相对潮湿的环境，也形成了湖南安化黑茶自己储存的特点。"},{"text":"【砖茶耐潮湿】"},{"text":"湖南黑茶中的砖类紧压茶，由于茶砖中具有的益生菌抑制了其它菌产生的缘故，只要出厂时含水量符合出厂标准，而空气湿度在75%以下，哪怕时间再长也不会霉变。无需冰箱冷藏，也不要塑料密封，但需存放在阴凉干燥之处，呈自然叠放即可，每年可翻动检查1~2次。如果环境过于潮湿，则易使砖茶发霉变质。若砖茶内生白霉、青霉等，则不能饮用。"},{"text":"【花卷茶抗潮湿】"},{"text":"湖南黑茶中的花卷茶，由于采用了棕叶、竹篾踩制紧压包装，使得外面的水分很难进入其内部。加上本身内部益生菌的作用，即便茶体表面有氧化现象，里面的茶也会继续陈化，对茶的陈化更有好处。当然要想陈化的好，适当的通风换气、避免长时间暴露在高温(35度以上)、高湿(80%以上的潮湿度)的要求依旧必不可少。"}]},{"name":"湖南黑砖茶饮用方法","content":[{"text":"清饮法"},{"text":"烹煮法："},{"text":"选用玻璃煮水器一套，过滤网、公道杯、品茗杯，冲泡先用沸水温杯烫壶，将预先备好的茯砖茶投入壶中，投茶量一般以茶水比1：20(可视茶原料及个人喜好增减茶量)为宜，沸水润茶后再注入冷泉水，煮至沸腾，将茶汤用过滤网沥入公道杯，再分茶入品茗杯，即可品饮，烹煮过程中茯茶的菌花香弥漫室内，赏心悦目。"},{"text":"泡饮法："},{"text":"即直接冲泡饮用的方法，可以是飘逸杯、瓷质三才杯，也可用\n        紫砂壶\n        、玻璃壶、投茶(茶水比1：20——30)、润茶、冲泡12秒钟，(可依茶的老嫩及个人口感爱好缩短或延长)、过滤等步骤，然后再注入品茗杯中，即可闻到茯砖茶特有的菌香，观其橙黄明亮的汤色，细品醇和甘爽的滋味了。"},{"text":"调用法"},{"text":"奶茶："},{"text":"先将茯茶敲碎投入沸水中，投茶比一般为1：20，煮10分钟后，加入相当于茶汤1/5——1/4的鲜奶(纯牛奶)煮开，然后用滤网去茶渣即成。还可根据要求调成咸味奶茶和甜味奶茶，甚至还可以加入一些炒制的阴米，既可饮又可嚼，香气十足，茶味浓郁。"},{"text":"菊花茯茶："},{"text":"将茯茶以茶水比1：20的量投入壶中，煮10分钟后，将茶汤滤出，再加入适量菊花，即成解渴生津、消暑的菊花茯茶了，若是酷暑季节，可待冷却后稍冰冻再饮用，格外清凉甘甜，是炎热季节降暑解渴的最佳饮品。"}]},{"name":"湖南黑砖茶冲泡方法","content":[{"text":"①功夫泡饮法：取茶适量，按功夫茶泡饮方式冲泡饮用。"},{"text":"②杯泡法：用如意杯或\n        紫砂壶\n        ，取适量茶先用沸水润茶，再浸泡1—2分钟后即可饮用。"},{"text":"③传统煮饮法：取茶适量，用沸水润茶后，再用冷水煮沸，停火滤茶后，分而饮之。"},{"text":"④奶茶饮法：按传统煮饮法煮好茶汤后，将奶、茶汤按1：5的比例调制，然后加适量盐，即调成具有西域特色的奶茶,橙红的茶汤与白色的奶充分混合后呈现粉红色,十分漂亮，称之为“红粉佳人”。"},{"text":"⑤冷饮法：按杯泡法或煮饮法滤好茶汤后，将茶汤放入冰箱或水井中冰镇后饮用,是夏天消暑解渴的佳品."}]},{"name":"湖南黑砖茶保健秘方","content":[{"text":"1、糖尿病配茶方法:"},{"text":"千两1:金茯1:黑砖3\\1的比例煮茶。前一天晚上开始煮茶，首先将煮茶器内加入1.5升矿泉水或山泉水，将水烧至80度左右，将搭配好的茶用沸水稍洗一下，放入煮茶器内，将水烧开后再煮3至5分钟关火，将煮茶器滤网与\n        黑茶\n        取出，让茶自助发酵一晚上，成为完美熟茶。早上起床空腹一定要喝一杯，然后白天没有时间控制，口渴时以茶代水就行。第二遍煮茶同样将煮茶器内加入同量的水，烧至80度左右，将黑茶与滤网一同放入，待烧开后再继续煮8至10分钟即可。第一个月要淡淡的喝，当有效果后再加另一款茶进去，进行调理稳定。"},{"text":"2、低血糖、血压、血尿糖配茶方法："},{"text":"天尖、荷香茯砖(补肾)、金茯早晚喝，若加蜂蜜茶泡20分钟后再喝。茶水放冰糖、百两茶泡脚。"},{"text":"3、高血糖、血压、血脂："},{"text":"金茯、千两饼(多)、黑砖、荷香茯砖(多)、天尖、百两茶(泡脚)。"},{"text":"4、高血脂、血压、血尿糖 ："},{"text":"天尖(3克)、百两(2克)金茯砖(10克)=15克每日煮茶水服用泡脚。"},{"text":"5、血糖、血脂、血压也有问题，胰岛高："},{"text":"国饮(多)、金茯、千两饼、天尖、百两茶(泡脚)。"},{"text":"6、糖尿病、脑中风："},{"text":"茯砖多款、荷香茯砖、千两饼、黑砖、天尖。"},{"text":"7、前列腺肥大、尿急、不净："},{"text":"金茯荷香(多)、天尖、老黑砖、百两茶。"},{"text":"8、肾衰："},{"text":"荷香金茯、天尖。"},{"text":"9、子宫肌瘤："},{"text":"金茯砖荷香、天尖、百两茶。"},{"text":"10、前列腺、尿急："},{"text":"金茯、荷香(多)、天尖、老砖、百两茶。"}]},{"name":"湖南黑砖茶历史故事","content":[{"text":"湖南省安化县生产\n        黑茶\n        历史悠久，早在明朝万历年间由户部正式定为运销西北地区以茶易马的\"官茶\"后，陕、甘、宁、晋地区的茶商，到朝廷在各地设置的茶马司以金(货币)易领\"茶引\"(按：明制茶课引规定：上引五千斤、中引四千斤、下引三千斤)，至安化大量采购黑茶砖，运销西北地区以茶易马(按：明洪武二十二年所定茶易马分上、中、下三等：上等马每匹一百二十斤、中等马每匹七十斤、下等马每匹五十斤)。大都运往兰州再转销陕、甘、青、新、宁、藏少数民族地区。明末清初西北地区的\"边茶\"十之八九由安化黑茶供应，多在陕西泾阳压成茶砖。1939年，湖南省茶叶管理处在安化县设厂大批量生产黑砖茶，产品分\"天、地、人、和\"四级，统称\"黑茶砖\"。1947年，安化茶叶公司设厂于江南镇，在茶砖面上印有\"八\"字，称\"八字茶砖\"，供不应求。新中国成立后，中国茶业公司安化砖茶厂(白沙溪茶厂前身)积极扩大生产，产品改称\"黑砖茶\"，主销西北少数民族地区。"},{"src":"\\heicha\\hunanheizhuancha\\img_20191122132733ogdcxd.jpg"},{"text":"1937年，抗日战争爆发，体积庞大的里引茶受运输条件的影响而大为减少，从而引起民国政府的高度重视，叫地方政府这里解决这一问题。时任湖南省茶叶管理处副处长的彭先泽先生，经过科学的实验和研究后，撰写了一篇题为《辟在安化不能压砖》一文，并见诸报端，引起政府和业界人士的高度重视。1939年5月，彭受托至安化试压黑砖茶，在安化的江南坪(江南镇)仿照湖北羊楼洞的压制方法，用木制压机压制样砖数片，送往当时的财政部贸易委员会检验。6月接到复电：“样砖色味俱佳，唯砖身松弛，诸嘱压紧。”7月于湘潭订购铸制手摇螺旋压机六部，12月压机运回。1940年1月装制，3月正是开工压制，其产品据当时旅居江南坪的泾阳茶商称：“砖身紧致，色泽艳丽，滋味醇厚，其品质在泾阳砖之上，并经重庆中国茶叶公司检验，交由中国茶叶公司销售，自此，安化开始压制黑砖茶。"}]},{"name":"湖南黑砖茶获奖情况","content":[{"text":"黑砖茶原产于湖南安化白沙溪，1939年前后开始生产。因砖面压有“湖南省砖茶厂压制”8个字，又称“八字砖”。因砖面用凸字字模，兰州市场称黑砖为“鼓字名牌安化黑砖”。现在年产量约5000吨，主销甘肃、宁夏、青海、新疆等省区，以兰州为集散地。"}]},{"name":"湖南黑砖茶黑茶的功效","content":[{"text":"黑砖茶属于\n        黑茶\n        ，黑茶中含有多种对人体健康有利的有效成分，中国西北民众素有“宁可三日无粮，不可一日无茶”之说。这个茶指的就是黑茶。我们实地作过调查，牧民们反映，一天不喝黑茶肚子就胀，三天不喝黑茶人全身无力，就象病了一场一样。牧民们把煮过的茶渣给马吃，马也要精神得多。我们还注意到这么一个现象，有些游牧民族放牧逐草而居，整个冬天根本没有蔬菜和水果，人体需要的植物性维生素完全取自于茶叶，一个冬天下来，人也健健康康，也从另一个方面证明黑茶对人体的健康功效。"},{"text":"另黑茶的功效与作用主要表现在以下四个方面：①清脂肪，减肥胖：黑茶中的多酚类及其氧化产物能溶解脂肪，促进脂类物质排出;还可活化蛋白质激酶，加速脂肪分解，降低体内脂肪的含量。因此黑茶被韩国人称为“瘦身茶”;日本人称为“美容茶”;台湾人称为“消食茶”。"},{"text":"②清肠胃，助消化：黑茶富含膳食纤维，具有调理肠胃的功能，清肠胃;且有益生菌参与，能改善肠道微生物环境，助消化。我国民间有利用老黑茶治疗腹胀、痢疾、消化不良的传统。"},{"text":"③清血管，降三高，黑茶中富含茶黄素，能软化血管，有效清除血管壁内的粥样物质，被称为“心血管的清道夫”;茶氨酸有效抑制血压升高，类黄酮物质能使血管壁松弛，增加血管的有效直径，降低血压;茶多糖具有类似胰岛素的作用，降低血糖含量;多酚类及其氧化产物能溶解脂肪，促进血管内脂类物质排出，降低血液中胆固醇的含量。"},{"text":"④清毒素，护肝肾 ：黑茶中独特的益生菌的功能因子和多酚类氧化物、儿茶素等多种化合物成分，参与人体内新陈代谢，对人体内脏具有特殊的净化功能，吸附体内的有毒物质(酒精、重金属、体内垃圾)排出体外，能深层排毒;又对病菌有抑制作用，保护肝肾。"},{"text":"黑茶一般具有消食去腻、降脂减肥、降三高、解酒、暖胃、安神的功效，另外，还有补充膳食营养、抑制动脉硬化等的作用。"}]}]}